<template>
	<div id="userFollow">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" style="color: #000000 !important;" @click="$router.back(-1)"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">我的关注</div>
      </div>
    </div>
    <div class="login_box">
      <van-list v-model="loading" :finished="finished" @load="getUserFollow">
        <div class="background_color_ffffff" style="margin: 5px 0;padding: 8px;overflow: hidden" v-for="(item,index) in followList" @click="toSeller(item)">
          <img :src="item.shop.shoplogo||'static/img/img_dptx_1_wdgz.png'" style="width: 50px;vertical-align: middle;"/>
          <label>{{item.shop.shopname||'暂无名称'}}</label>
          <label class="float_right font_12" style="margin-top: 20px;">
            <label class="color_8b8b8b" style="padding: 0px 8px">{{item.shop.addtime||'暂无数据'}}</label>
          </label>
        </div>
      </van-list>
    </div>
	</div>
</template>
<script>
	export default {
		name: 'userFollow',
		data() {
			return {
        loading: false,
        finished: false,
        pageNum: 1,
        followList:[],
        userId:localStorage.getItem('userId')
			}
		},
		methods:{
      toSeller(dataMap){
        this.$store.commit('setSellerInfo', dataMap.shop);
        this.$router.push('seller');
      },
      getUserFollow(){
        this.loading = true;
        let $this = this;
        let obj = {
          userid: $this.userId,
          pageNum: $this.pageNum
        };
        let qs = require('qs');
        $this.$ajax.post("follow/getfollowlist", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            if(rpdata.data.data.data.length>0){
              $this.followList = $this.followList.concat(rpdata.data.data.data);
              $this.pageNum++;
              $this.loading = false;
            }else if(rpdata.data.data.data.length==0){
              $this.finished = true;
              $this.loading = false;
            }
          }
        })
        .catch(function(err) {})
      },
		}
	}
</script>

<style scoped>
  .login_box{
    margin-top: 54px;
  }
</style>



